import React from 'react'
import { IoIosCog } from "react-icons/io";
import { NavLink } from 'react-router-dom';

const Navbar = () => {
    return (
    <>
        <div className="container-fluid nav_bg mt-1">
            <div className="row">
                <div className="col-10 mx-auto">
                <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <div className="container-fluid">
                <NavLink className="navbar-brand" to="/"><IoIosCog className="si" /></NavLink>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto  mb-2 mb-lg-0">
                        <li className="nav-item">
                    <NavLink activeClassName="ac" className="nav-link active" aria-current="page" to="/">Login</NavLink>
                        </li>
                        <li className="nav-item">
                    <NavLink activeClassName="ac" className="nav-link" to="/signup">Signup</NavLink>
                        </li>
                    </ul>
                    </div>
                </div>
                </nav>
                </div>
            </div>
        </div>
        </>
    )
}

export default Navbar
